import Uppy from '@uppy/core'
import XHRUpload from '@uppy/xhr-upload'

import { addQueryToUrl } from '../utils/util'
import { IUploadConfig } from './interface'

function createUploader(config: IUploadConfig): Uppy {
  // è·åéç½®
  const {
    server = '',
    fieldName = '',
    maxFileSize = 10 * 1024 * 1024, // 10M
    maxNumberOfFiles = 100, // æå¤å¤å°ä¸ªæä»¶
    meta = {},
    metaWithUrl = false,
    headers = {},
    withCredentials = false,
    timeout = 10 * 1000, // 10s
    onBeforeUpload = files => files,
    onSuccess = (_file, _res) => {
      /* on success */
    },
    onError = (file, err, res?) => {
      console.error(`${file.name} upload error`, err, res)
    },
    onProgress = _progress => {
      /* on progress */
    },
  } = config

  // å¤æ­éç½®é¡¹
  if (!server) {
    throw new Error('Cannot get upload server address\næ²¡æéç½®ä¸ä¼ å°å')
  }
  if (!fieldName) {
    throw new Error('Cannot get fieldName\næ²¡æéç½® fieldName')
  }

  // æ¯å¦è¦è¿½å  url åæ°
  let url = server

  if (metaWithUrl) {
    url = addQueryToUrl(url, meta)
  }

  // çæ uppy å®ä¾ï¼åèææ¡£ https://uppy.io/docs/uppy/
  const uppy = new Uppy({
    onBeforeUpload,
    restrictions: {
      maxFileSize,
      maxNumberOfFiles,
    },
    meta, // èªå®ä¹æ·»å å° formData ä¸­çåæ°
    ...config.uppyConfig, // æ¯æä¼ å¥æ´å¤ Uppy éç½®é¡¹
  }).use(XHRUpload, {
    endpoint: url,
    headers,
    formData: true,
    fieldName,
    bundle: true,
    withCredentials,
    timeout,
    ...config.xhrConfig, // æ¯æä¼ å¥æ´å¤ XHRUpload éç½®é¡¹
  })

  // åä¸ª callback
  // onSuccess æ¯ä¸ª file ä¸ä¼ æåé½ä¼è§¦åï¼æ¹ç¨ complete ä¾¿äº files ä¸ä¼ 
  uppy.on('complete', result => {
    const file = result.successful[0]
    const { response } = file
    const { body = {} } = response ?? {}

    try {
      onSuccess(file, body)
    } catch (err) {
      console.error('wangEditor upload file - onSuccess error', err)
    }
  })

  uppy.on('progress', progress => {
    // progress å¼èå´ï¼ 0 - 100
    if (progress < 1) { return }
    onProgress(progress)
  })

  // uppy.on('error', error => {
  //   console.error('wangEditor file upload error', error.stack)
  // })

  uppy.on('upload-error', (file, error, response) => {
    try {
      // æç¨æ·ä¼ å¥çç¬¬ä¸æ¹ä»£ç ï¼å¾ç¨ try catch åè£¹
      onError(file, error, response)
    } catch (err) {
      console.error('wangEditor upload file - onError error', err)
    }
  })

  uppy.on('restriction-failed', (file, error) => {
    try {
      // æç¨æ·ä¼ å¥çç¬¬ä¸æ¹ä»£ç ï¼å¾ç¨ try catch åè£¹
      // @ts-ignore
      onError(file, error)
    } catch (err) {
      console.error('wangEditor upload file - onError error', err)
    }
  })

  // è¿åå®ä¾
  return uppy
}

export default createUploader
