import { IPositionStyle } from '../interface'

/**
 * è·å textContainer å°ºå¯¸åå®ä½
 * @param editor editor
 */
export function getTextContainerRect(editor: IDomEditor): {
  top: number
  left: number
  width: number
  height: number
} | null {
  const textarea = DomEditor.getTextarea(editor)

  // è·å textareaContainer
  const $textareaContainer = textarea.$textAreaContainer
  const width = $textareaContainer.width()
  const height = $textareaContainer.height()
  const { top, left } = $textareaContainer.offset()

  return {
    top, left, width, height,
  }
}

/**
 * æ ¹æ®éåºï¼è®¡ç®å®ä½ï¼ç¨äº modal hoverbarï¼
 * @param editor editor
 */
export function getPositionBySelection(editor: IDomEditor): Partial<IPositionStyle> {
  // é»è®¤æåµä¸ { top: 0, left: 0 }
  const defaultStyle = { top: '0', left: '0' }

  const { selection } = editor

  if (selection == null) { return defaultStyle } // é»è®¤ position

  // è·å textContainer rect
  const containerRect = getTextContainerRect(editor)

  if (containerRect == null) { return defaultStyle } // é»è®¤ position
  const {
    top: containerTop,
    left: containerLeft,
    width: containerWidth,
    height: containerHeight,
  } = containerRect

  // è·åå½åéåºç rect
  const range = DomEditor.toDOMRange(editor, selection)
  const rects = range.getClientRects()
  const rangeRect = rects ? rects[0] : null

  if (rangeRect == null) { return defaultStyle } // é»è®¤ position
  const { top: rangeTop, left: rangeLeft } = rangeRect

  // å­å¨è®¡ç®ç»æ
  const positionStyle: Partial<IPositionStyle> = {}

  // è·å éåº top left å container top left çå·®å¼ï¼< 0 åä½¿ç¨ 0ï¼
  const relativeTop = rangeTop - containerTop
  const relativeLeft = rangeLeft - containerLeft

  // å¤æ­æ°´å¹³ä½ç½®ï¼ modal/bar æ¾ç¤ºå¨éåºå·¦ä¾§ï¼è¿æ¯å³ä¾§ï¼
  if (relativeLeft > containerWidth / 2) {
    // éåº left å¤§äº containerWidth/2 ï¼éåºå¨ container çå³ä¾§ï¼ï¼å modal/bar æ¾ç¤ºå¨éåºå·¦ä¾§
    const r = containerWidth - relativeLeft

    positionStyle.right = `${r + 5}px` // 5px é´é
  } else {
    // å¦åï¼éåºå¨ container çå·¦ä¾§ï¼ï¼modal/bar æ¾ç¤ºå¨éåºå³ä¾§
    positionStyle.left = `${relativeLeft + 5}px` // 5px é´é
  }

  // å¤æ­åç´çä½ç½®ï¼ modal/bar æ¾ç¤ºå¨éåºä¸é¢ï¼è¿æ¯ä¸é¢ï¼
  if (relativeTop > containerHeight / 2) {
    // éåº top  > containerHeight/2 ï¼éåºå¨ container çä¸åé¨åï¼ï¼å modal/bar æ¾ç¤ºå¨éåºçä¸é¢
    const b = containerHeight - relativeTop

    positionStyle.bottom = `${b + 5}px` // 5px é´é
  } else {
    // å¦åï¼éåºå¨ container çä¸åé¨åï¼ï¼å modal/bar æ¾ç¤ºå¨éåºçä¸é¢
    const lastRect = rects[rects.length - 1]

    let t = lastRect.top - containerTop + lastRect.height

    if (t < 0) { t = 0 }
    positionStyle.top = `${t + 5}px` // 5px é´é
  }

  return positionStyle
}

/**
 * æ ¹æ® node ï¼è®¡ç®å®ä½ï¼ç¨äº modal hoverbarï¼
 * @param editor editor
 * @param node slate node
 * @param type 'modal'/'bar'
 */
export function getPositionByNode(
  editor: IDomEditor,
  node: Node,
  type: string = 'modal',
): Partial<IPositionStyle> {
  // é»è®¤æåµä¸ { top: 0, left: 0 }
  const defaultStyle = { top: '0', left: '0' }

  const { selection } = editor

  if (selection == null) { return defaultStyle } // é»è®¤ position

  // æ ¹æ® node è·å elem
  const isVoidElem = Element.isElement(node) && editor.isVoid(node)
  const isInlineElem = Element.isElement(node) && editor.isInline(node)
  const elem = NODE_TO_ELEMENT.get(node)

  if (elem == null) { return defaultStyle } // é»è®¤ position
  let {
    top: elemTop,
    // eslint-disable-next-line prefer-const
    left: elemLeft,
    height: elemHeight,
    // eslint-disable-next-line prefer-const
    width: elemWidth,
  } = elem.getBoundingClientRect()

  if (isVoidElem) {
    // void node ï¼éæ°è®¡ç® top å height
    const voidElem = getFirstVoidChild(elem)

    if (voidElem != null) {
      const { top, height } = voidElem.getBoundingClientRect()

      elemTop = top
      elemHeight = height
    }
  }

  // è·å textContainer rect
  const containerRect = getTextContainerRect(editor)

  if (containerRect == null) { return defaultStyle } // é»è®¤ position
  const {
    top: containerTop,
    left: containerLeft,
    width: containerWidth,
    height: containerHeight,
  } = containerRect

  // å­å¨è®¡ç®ç»æ
  const positionStyle: Partial<IPositionStyle> = {}

  // è·å elem top left å container top left çå·®å¼ï¼< 0 åä½¿ç¨ 0ï¼
  const relativeTop = elemTop - containerTop
  const relativeLeft = elemLeft - containerLeft

  if (type === 'bar') {
    // bar - 1. left å¯¹é½ elem.left ï¼2. å°½éæ¾ç¤ºå¨ elem ä¸æ¹
    positionStyle.left = `${relativeLeft}px`
    if (relativeTop > 40) {
      // top > 40 åæ¾ç¤ºå¨ä¸æ¹
      positionStyle.bottom = `${containerHeight - relativeTop + 5}px` // 5px é´é
    } else {
      // å¦åï¼æ¾ç¤ºå¨ä¸æ¹
      positionStyle.top = `${relativeTop + elemHeight + 5}px` // 5px é´é
    }

    return positionStyle
  }

  if (type === 'modal') {
    // modal - 1. top å elem éè¦è®¡ç®ï¼å°½éä¸é®æ¡ elem

    // æ°´å¹³
    if (!isVoidElem) {
      // é void node - left å elem left å¯¹é½
      positionStyle.left = `${relativeLeft}px`
    } else if (isInlineElem) {
      // inline void node éè¦è®¡ç®
      if (relativeLeft > (containerWidth - elemWidth) / 2) {
        // elem å¨ container çå³ä¾§ï¼å modal æ¾ç¤ºå¨ elem å·¦ä¾§
        positionStyle.right = `${containerWidth - relativeLeft + 5}px`
      } else {
        // å¦å elem å¨ container å·¦ä¾§ï¼å modal æ¾ç¤ºå¨ elem å³ä¾§
        positionStyle.left = `${relativeLeft + elemWidth + 5}px`
      }
    } else {
      // block void node æ°´å¹³é å·¦å³å¯
      positionStyle.left = '20px'
    }

    // åç´
    if (isVoidElem) {
      // void node - top å elem top å¯¹é½
      let t = relativeTop

      if (t < 0) { t = 0 } // top ä¸è½å°äº 0
      positionStyle.top = `${t}px`
      // é void node ï¼è®¡ç® top
    } else if (relativeTop > (containerHeight - elemHeight) / 2) {
      // elem å¨ container çä¸åé¨åï¼å modal æ¾ç¤ºå¨ elem ä¸æ¹
      positionStyle.bottom = `${containerHeight - relativeTop + 5}px`
    } else {
      // elem å¨ container çä¸åé¨åï¼å modal æ¾ç¤ºå¨ elem ä¸æ¹
      let t = relativeTop + elemHeight

      if (t < 0) { t = 0 }
      positionStyle.top = `${t + 5}px`
    }

    return positionStyle
  }

  throw new Error(`type '${type}' is invalid`)
}

/**
 * å¼æ­¥ä¿®æ­£ position ï¼ä¸è½è¶åº textContainer è¾¹ç
 * @param editor editor
 * @param $positionElem modal/bar
 */
export function correctPosition(editor: IDomEditor, $positionElem: Dom7Array) {
  // å¼æ­¥ï¼å¦å DOM å°æªæ¸²æ
  promiseResolveThen(() => {
    // è·å textContainer rect
    const containerRect = getTextContainerRect(editor)

    if (containerRect == null) { return }
    const {
      top: containerTop,
      left: containerLeft,
      width: containerWidth,
      height: containerHeight,
    } = containerRect

    // è·å modal bar ç rect
    const { top: positionElemTop, left: positionElemLeft } = $positionElem.offset()
    const positionElemWidth = $positionElem.width()
    const positionElemHeight = $positionElem.height()
    const relativeTop = positionElemTop - containerTop
    const relativeLeft = positionElemLeft - containerLeft

    // è·å modal bar è®¾ç½®ç style
    const styleStr = $positionElem.attr('style')

    if (styleStr.indexOf('top') >= 0) {
      // è®¾ç½®äº top ï¼åæå¯è½è¶è¿ textContainer çä¸è¾¹ç
      const d = relativeTop + positionElemHeight - containerHeight

      if (d > 0) {
        // å·²è¶è¿ textContainer çä¸è¾¹çï¼åä¸ç§»
        const curTopStr = $positionElem.css('top')
        const curTop = parseInt(curTopStr.toString(), 10)
        let newTop = curTop - d

        if (newTop < 0) { newTop = 0 } // ä¸è½è¶è¿ textContainer ä¸è¾¹ç
        $positionElem.css('top', `${newTop}px`)
      }
    }

    if (styleStr.indexOf('bottom') >= 0) {
      // è®¾ç½®äº bottom ï¼åæå¯è½è¶è¿ textContainer çä¸è¾¹ç
      if (positionElemTop < 0) {
        // å·²è¶åºäºä¸è¾¹ç
        const curBottomStr = $positionElem.css('bottom')
        const curBottom = parseInt(curBottomStr.toString(), 10)
        const newBottom = curBottom - Math.abs(positionElemTop) // ä¿è¯ä¸è¾¹çå textContainer å¯¹é½å³å¯ï¼ä¸è¾¹çä¸ç®¡

        $positionElem.css('bottom', `${newBottom}px`)
      }
    }

    if (styleStr.indexOf('left') >= 0) {
      // è®¾ç½®äº left ï¼åæå¯è½è¶è¿ textContainer çå³è¾¹ç
      const d = relativeLeft + positionElemWidth - containerWidth

      if (d > 0) {
        // å·²è¶è¿ textContainer çå³è¾¹çï¼éå·¦ç§»
        const curLeftStr = $positionElem.css('left')
        const curLeft = parseInt(curLeftStr.toString(), 10)
        let newLeft = curLeft - d

        if (newLeft < 0) { newLeft = 0 } // ä¸è½è¶è¿ textContainer å·¦è¾¹ç
        $positionElem.css('left', `${newLeft}px`)
      }
    }

    if (styleStr.indexOf('right') >= 0) {
      // è®¾ç½®äº right ï¼åæå¯è½è¶è¿ textContainer çå·¦è¾¹ç
      if (positionElemLeft < 0) {
        // å·²è¶åºäºå·¦è¾¹ç
        const curRightStr = $positionElem.css('right')
        const curRight = parseInt(curRightStr.toString(), 10)
        const newRight = curRight - Math.abs(positionElemLeft) // ä¿è¯å·¦è¾¹çå textContainer å¯¹é½å³å¯ï¼å³è¾¹çä¸ç®¡

        $positionElem.css('right', `${newRight}px`)
      }
    }
  })
}
