import debounce from 'lodash.debounce'
import {
  Editor, Element, Node, Path, Range, Text,
} from 'slate'

import { CustomElement } from '../../../../custom-types'
import { EditorEvents } from '../../config/interface'
import { DomEditor } from '../../editor/dom-editor'
import { IDomEditor } from '../../editor/interface'
import { i18nListenLanguage } from '../../i18n'
import $ from '../../utils/dom'
import { promiseResolveThen } from '../../utils/util'
import { BAR_ITEM_TO_EDITOR, HOVER_BAR_TO_EDITOR } from '../../utils/weak-maps'
import { createBarItem, IBarItem } from '../bar-item/index'
import { gen$barItemDivider } from '../helpers/helpers'
import { correctPosition, getPositionByNode, getPositionBySelection } from '../helpers/position'
import {
  IButtonMenu, IDropPanelMenu, IModalMenu, ISelectMenu,
} from '../interface'
import { MENU_ITEM_FACTORIES } from '../register'

type MenuType = IButtonMenu | ISelectMenu | IDropPanelMenu | IModalMenu

/**
 * æ¯å¦éä¸­äº text ï¼ç¨äº text hoverbarKeysï¼
 * @param editor editor
 * @param n node
 */
function isSelectedText(editor: IDomEditor, n: Node) {
  const { selection } = editor

  if (selection == null) { return false } // æ éåº
  if (Range.isCollapsed(selection)) { return false } // æªéä¸­æå­ï¼éåºçæ¯æå ç

  const selectedElems = DomEditor.getSelectedElems(editor)
  const notMatch = selectedElems.some((elem: CustomElement) => {
    if (editor.isVoid(elem)) { return true }

    const { type } = elem

    if (['pre', 'code', 'table'].includes(type)) { return true }
    return false
  })

  if (notMatch) { return false }

  if (Text.isText(n)) { return true } // å¹é text node
  return false
}

class HoverBar {
  private readonly $elem = $('<div class="w-e-bar w-e-bar-hidden w-e-hover-bar"></div>')

  private menus: { [key: string]: MenuType } = {}

  private hoverbarItems: IBarItem[] = []

  private prevSelectedNode: Node | null = null // ä¸ä¸æ¬¡éä¸­ç node

  private isShow = false

  private lngListen: () => void = () => {}

  constructor() {
    // å¼æ­¥ï¼å¦åè·åä¸å° DOM å editor
    promiseResolveThen(() => {
      const editor = this.getEditorInstance()

      // å° elem æ¸²æä¸º DOM
      const $elem = this.$elem
      // @ts-ignore

      $elem.on('mousedown', e => e.preventDefault(), { passive: false }) // é²æ­¢ç¹å»å¤±ç¦
      const textarea = DomEditor.getTextarea(editor)

      textarea.$textAreaContainer.append($elem)

      // ç»å® editor onchange
      editor.on(EditorEvents.CHANGE, this.changeHoverbarState)

      // æ»å¨æ¶éè
      const hideAndClean = this.hideAndClean.bind(this)

      editor.on(EditorEvents.SCROLL, hideAndClean)

      // fullScreen æ¶éè
      editor.on(EditorEvents.FULLSCREEN, hideAndClean)
      editor.on(EditorEvents.UNFULLSCREEN, hideAndClean)
    })

    // çå¬è¯­è¨åæ´
    this.lngListen = i18nListenLanguage(() => {
      // æ¸ç©ºmenuç¼å­
      this.menus = {}
      // åæ¢è¯­è¨ç´æ¥å³é­
      this.hideAndClean()
      // xxx
      const editor = this.getEditorInstance()

      editor.deselect()
    })
  }

  getMenus() {
    return this.menus
  }

  hideAndClean() {
    const $elem = this.$elem

    $elem.removeClass('w-e-bar-show').addClass('w-e-bar-hidden')

    // åæ¶åæ¸ç©ºåå®¹ï¼å¦åå½±åä¸æ¬¡
    this.hoverbarItems = []
    $elem.empty()

    this.isShow = false
  }

  /**
   * å¤æ­ hoverbar æ¯å¦å¨ç½é¡µä¸é¨ï¼
   * å¦ææ¯ï¼SelectList å DropPanel è¦æ¾ç¤ºå¨ hoverbar ä¸é¢
   */
  private checkPositionBottom() {
    const $elem = this.$elem

    let isBottom = false
    const innerHeight = typeof window !== 'undefined' ? window.innerHeight : null
    const minDistance = 360 // è·ç¦»åºé¨æå° 360px

    if (innerHeight && innerHeight >= minDistance) {
      const { bottom } = $elem[0].getBoundingClientRect()

      if (innerHeight - bottom < minDistance) {
        // hoverbar è·ç¦»åºé¨ä¸è¶³ 360
        isBottom = true
      }
    }
    if (isBottom) {
      $elem.addClass('w-e-bar-bottom')
    } else {
      $elem.removeClass('w-e-bar-bottom')
    }
  }

  private show() {
    this.$elem.removeClass('w-e-bar-hidden').addClass('w-e-bar-show')
    this.isShow = true

    // å¤æ­ hoverbar æ¯å¦å¨ç½é¡µä¸é¨
    this.checkPositionBottom()
  }

  private changeItemsState() {
    promiseResolveThen(() => {
      this.hoverbarItems.forEach(item => {
        item.changeMenuState()
      })
    })
  }

  private registerItems(menuKeys: string[]) {
    const $elem = this.$elem

    menuKeys.forEach(key => {
      if (key === '|') {
        // åå²çº¿
        const $divider = gen$barItemDivider()

        $elem.append($divider)
        return
      }

      // æ­£å¸¸èå
      this.registerSingleItem(key)
    })
  }

  // æ³¨ååä¸ª bar item
  private registerSingleItem(key: string) {
    const editor = this.getEditorInstance()

    // å°è¯ä»ç¼å­ä¸­è·å
    const { menus } = this
    let menu = menus[key]

    if (menu == null) {
      // ç¼å­è·åå¤±è´¥ï¼åéæ°åå»º
      const factory = MENU_ITEM_FACTORIES[key]

      if (factory == null) {
        throw new Error(`Not found menu item factory by key '${key}'`)
      }
      if (typeof factory !== 'function') {
        throw new Error(`Menu item factory (key='${key}') is not a function`)
      }

      // åå»º barItem å¹¶è®°å½ç¼å­
      menu = factory()
      menus[key] = menu
    }

    // æ¿æ¢ icon svg
    const menuConf = editor.getMenuConfig(key)

    if (menuConf && menuConf.iconSvg !== undefined) {
      menu.iconSvg = menuConf.iconSvg
    }

    const barItem = createBarItem(key, menu)

    this.hoverbarItems.push(barItem)

    // ä¿å­ barItem å editor çå³ç³»
    BAR_ITEM_TO_EDITOR.set(barItem, editor)

    // æ·»å  DOM
    const $elem = this.$elem

    $elem.append(barItem.$elem)
  }

  private setPosition(node: Node) {
    const editor = this.getEditorInstance()
    const $elem = this.$elem

    $elem.attr('style', '') // åæ¸ç©º style ï¼åéæ°è®¾ç½®

    if (Element.isElement(node)) {
      // æ ¹æ® elem node å®ä½
      const positionStyle = getPositionByNode(editor, node, 'bar')

      $elem.css(positionStyle)
      correctPosition(editor, $elem) // ä¿®æ­£ position é¿åè¶åº textContainer è¾¹ç
      return
    }
    if (Text.isText(node)) {
      // text node ï¼æ ¹æ®éåºå®ä½
      const positionStyle = getPositionBySelection(editor)

      $elem.css(positionStyle)
      correctPosition(editor, $elem) // ä¿®æ­£ position é¿åè¶åº textContainer è¾¹ç
      return
    }
    // å¶ä»æåµï¼é elem é text ï¼ä¸å¤ç
    throw new Error('hoverbar.setPosition error, current selected node is not elem nor text')
  }

  /**
   * è·åéä¸­ç node ï¼ä»¥åå¯¹åºç menu keys
   */
  private getSelectedNodeAndMenuKeys(): { node: Node; menuKeys: string[] } | null {
    const editor = this.getEditorInstance()

    if (editor.selection == null) {
      return null
    }

    // è·å hover bar éç½®
    const keysConf = this.getHoverbarKeysConf()
    // å¼å§å¹é
    let matchNode: Node | null = null
    let matchMenuKeys: string[] = []

    // eslint-disable-next-line guard-for-in
    for (const elemType in keysConf) {
      const conf = keysConf[elemType]
      const { match, menuKeys = [] } = conf

      // å®ä¹äº match åç¨ match ãæªå®ä¹ match åç¨ elemType
      const matchFn = match || ((_editor: IDomEditor, n: Node) => DomEditor.checkNodeType(n, elemType))

      const [nodeEntry] = Editor.nodes(editor, {
        match: n => matchFn(editor, n),
        universal: true,
      })

      // å¹éæåï¼æ¾å°ç¬¬ä¸ä¸ªå°±åæ­¢ï¼ä¸åç»§ç»­æ¾äºï¼
      if (nodeEntry != null) {
        matchNode = nodeEntry[0]
        matchMenuKeys = menuKeys
        break
      }
    }

    // æªå¹éæå
    if (matchNode == null || matchMenuKeys.length === 0) { return null }

    // å¹éæå
    return {
      node: matchNode,
      menuKeys: matchMenuKeys,
    }
  }

  /**
   * editor onChange æ¶è§¦åï¼æ¶å DOM æä½ï¼å é²æï¼
   */
  changeHoverbarState = debounce(() => {
    // è·åéä¸­ç node ï¼ä»¥åå¯¹åºç menu keys
    const { isShow } = this
    const { node = null, menuKeys = [] } = this.getSelectedNodeAndMenuKeys() || {}
    const editor = this.getEditorInstance()

    if (node != null) {
      this.changeItemsState() // æ´æ°èåç¶æ
    }

    if (node && Element.isElement(node)) {
      // éä¸­äº elem nodeï¼ä¸å¯ä»¥æ¯ text nodeï¼
      if (isShow) {
        // hoverbar å½åå·²æ¾ç¤º
        const samePath = this.isSamePath(node, this.prevSelectedNode)
        const isBlock = Editor.isBlock(editor, node)

        if (!isBlock && samePath) {
          // ä¸æ¯ååç´ ä¸åä¹åéä¸­ç node path ç¸å ââ æ»¡è¶³è¿äºæ¡ä»¶ï¼å³ç»æ­¢
          return
        }
      }
    }

    // éæ©äºæ°ç nodeï¼æéåºæ¯ nullï¼ï¼åéè
    this.hideAndClean()

    if (node != null) {
      // éä¸­äºæ°ç node
      this.registerItems(menuKeys)
      this.setPosition(node)
      this.show()
    }

    // æåï¼éæ°è®°å½ prevSelectedNode ï¼éè¦
    this.prevSelectedNode = node
  }, 200)

  private getEditorInstance(): IDomEditor {
    const editor = HOVER_BAR_TO_EDITOR.get(this)

    if (editor == null) { throw new Error('Can not get editor instance') }
    return editor
  }

  private getHoverbarKeysConf() {
    const editor = this.getEditorInstance()
    const { hoverbarKeys = {} } = editor.getConfig()

    const textHoverbarKeys = hoverbarKeys.text

    if (textHoverbarKeys && textHoverbarKeys.match == null) {
      // å¯¹ text hoverbarKeys å¢å  match å½æ°ï¼å¦åæ æ³å¤æ­æ¯å¦éä¸­äº textï¼
      textHoverbarKeys.match = isSelectedText
    }

    return hoverbarKeys
  }

  /**
   * æ£æ¥ä¸¤ä¸ª node æ¯å¦ path ç¸ç­
   */
  private isSamePath(node1: Node | null, node2: Node | null) {
    if (node1 == null || node2 == null) {
      return false
    }

    const path1 = DomEditor.findPath(null, node1)
    const path2 = DomEditor.findPath(null, node2)
    const res = Path.equals(path1, path2)

    return res
  }

  /**
   * éæ¯ hoverbar
   */
  destroy() {
    // fix https://github.com/wangeditor-next/wangeditor-next-v5/issues/410
    this.changeHoverbarState.cancel()
    // éæ¯ DOM
    this.$elem.remove()

    // éæ¯è¯­è¨çå¬
    this.lngListen?.()

    // æ¸ç©ºå±æ§
    this.menus = {}
    this.hoverbarItems = []
    this.prevSelectedNode = null
  }
}

export default HoverBar
