import forEach from 'lodash.foreach'
import { nanoid } from 'nanoid'

type PromiseCallback = (value: void) => void | PromiseLike<void>

/**
 * è·åéæºæ°å­ç¬¦ä¸²
 * @param prefix åç¼
 * @returns éæºæ°å­ç¬¦ä¸²
 */
export function genRandomStr(prefix: string = 'r'): string {
  return `${prefix}-${nanoid()}`
}

export function promiseResolveThen(fn: Function) {
  Promise.resolve().then(fn as PromiseCallback)
}

/**
 * è¿½å  url query åæ°
 * @param url url
 * @param data data
 */
export function addQueryToUrl(url: string, data: object): string {
  let [urlWithoutHash, hash] = url.split('#')

  // æ¼æ¥ query string
  const queryArr: string[] = []
  forEach(data, (val, key) => {
    queryArr.push(`${key}=${val}`)
  })
  const queryStr = queryArr.join('&')

  // æ¼æ¥å° url
  if (urlWithoutHash.indexOf('?') > 0) {
    // å·²æ query
    urlWithoutHash = `${urlWithoutHash}&${queryStr}`
  } else {
    // æ²¡æ query
    urlWithoutHash = `${urlWithoutHash}?${queryStr}`
  }

  // è¿åæ¼æ¥å¥½ç url
  if (hash) {
    return `${urlWithoutHash}#${hash}`
  } else {
    return urlWithoutHash
  }
}

/**
 * æ¿æ¢ html ç¹æ®å­ç¬¦ï¼å¦ > æ¿æ¢ä¸º &gt;
 * @param str html str
 */
export function replaceHtmlSpecialSymbols(str: string) {
  return (
    str
      /**
       * éå°ä¸¤ä¸ªç©ºæ ¼æ¶ææ¿æ¢ï¼ä¸ä¸ªç©ºæ ¼ä¸æ¿æ¢
       * ä¸¤ä¸ªè±æåè¯ä¹é´æä¸ä¸ªç©ºæ ¼ï¼å°±ä¸ç¨æ¿æ¢ï¼å¦åæ æ³é»è®¤æ¢è¡ issue #4403
       */
      .replace(/ {2}/g, ' &nbsp;')

      .replace(/</g, '&lt;')
      .replace(/>/g, '&gt;')
      .replace(/Â®/g, '&reg;')
      .replace(/Â©/g, '&copy;')
      .replace(/â¢/g, '&trade;')
  )
}

/**
 *ãåè½¬ãæ¿æ¢ html ç¹æ®å­ç¬¦ï¼å¦ &gt; æ¿æ¢ä¸º >
 * @param str html str
 */
export function deReplaceHtmlSpecialSymbols(str: string) {
  return str
    .replace(/&nbsp;/g, ' ')
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&reg;/g, 'Â®')
    .replace(/&copy;/g, 'Â©')
    .replace(/&trade;/g, 'â¢')
    .replace(/&quot;/g, '"')
}

export const toString = (val: unknown): string => Object.prototype.toString.call(val)
