/**
 * @description parse elem html
 * @author wangfupeng
 */

import $, { Dom7Array } from 'dom7'
import {
  Descendant, Editor, Element, Text,
} from 'slate'

import { IDomEditor } from '../editor/interface'
import {
  DOMElement, isDOMElement, isDOMText,
} from '../utils/dom'
import { replaceSpace160 } from './helper'
import { PARSE_ELEM_HTML_CONF, PARSE_STYLE_HTML_FN_LIST, ParseElemHtmlFnType } from './index'
import parseElemHtml from './parse-elem-html'

/**
 * å¾ children æåä¸ä¸ª itemï¼å¦ææ¯ text nodeï¼ æå¥æå­
 * @param children children
 * @param str str
 * @returns æ¯å¦æå¥æå
 */
function tryInsertTextToChildrenLastItem(children: Descendant[], str: string): boolean {
  const len = children.length

  if (len) {
    const lastItem = children[len - 1]

    if (Text.isText(lastItem)) {
      const keys = Object.keys(lastItem)

      if (keys.length === 1 && keys[0] === 'text') {
        // lastItem å¿é¡»æ¯çº¯ææ¬ï¼æ²¡æ marks
        lastItem.text += str
        return true
      }
    }
  }
  return false
}

/**
 * çæ slate node children
 * @param $elem $elem
 * @param editor editor
 */
function genChildren($elem: Dom7Array, editor: IDomEditor): Descendant[] {
  const children: Descendant[] = []

  // void nodeï¼ html ä¸­ç¼è¾çï¼å¦ video ç html ä¸­ä¼æ data-w-e-is-void å±æ§ ï¼ï¼ä¸éè¦çæ children
  const isVoid = $elem.attr('data-w-e-is-void') != null

  if (isVoid) {
    return children
  }

  const childNodes = $elem[0].childNodes

  // å¤çç©ºè¡ï¼åªæä¸ä¸ª child ï¼æ¯ <br>ï¼
  if (childNodes.length === 1) {
    if (childNodes[0].nodeName === 'BR') {
      children.push({ text: '' })
      return children // ç´æ¥è¿å
    }
  }

  // éå DOM å­èç¹ï¼çæ slate elem node children
  childNodes.forEach(child => {
    if (isDOMElement(child)) {
      // <br> ï¼åå¾ children æåä¸ä¸ªåç´ ï¼å¦ææ¯ text ï¼è¿½å  `\n`
      if (child.nodeName === 'BR') {
        // å°è¯æ text æå¥å°æåä¸ä¸ª children
        const res = tryInsertTextToChildrenLastItem(children, '\n')

        if (!res) {
          // è¥æå¥å¤±è´¥ï¼åæ°å»º item
          children.push({ text: '\n' })
        }
        return
      }

      // å¶ä» elem
      const $child = $(child)
      const parsedRes = parseElemHtml($child, editor)

      if (Array.isArray(parsedRes)) {
        parsedRes.forEach(el => children.push(el))
      } else {
        children.push(parsedRes)
      }
      return
    }
    if (isDOMText(child)) {
      // text
      let text = child.textContent || ''

      if (text.trim() === '' && text.indexOf('\n') >= 0) {
        // ææ¢è¡ï¼ä½æ å®éåå®¹
        return
      }

      if (text) {
        // æ charCode 160 çç©ºæ ¼ï¼`&nbsp` è½¬æ¢çï¼ï¼æ¿æ¢ä¸º charCode 32 çç©ºæ ¼ï¼JS é»è®¤çï¼
        text = replaceSpace160(text)

        // å°è¯æ text æå¥å°æåä¸ä¸ª children
        const res = tryInsertTextToChildrenLastItem(children, text)

        if (!res) {
          // è¥æå¥å¤±è´¥ï¼åæ°å»º item
          children.push({ text })
        }
      }

    }
  })
  return children
}

/**
 * é»è®¤ç parseElemHtml ï¼ç´æ¥è½¬æ¢ä¸º paragraph
 * @param elem elem
 * @param children children
 */
function defaultParser(elem: DOMElement, _children: Descendant[], _editor: IDomEditor): Element {
  return {
    type: 'paragraph',
    children: [{ text: $(elem).text().replace(/\s+/gm, ' ') }],
  }
}

/**
 * è·åå½å html åç´ ç parseElemHtml å½æ°
 * @param $elem $elem
 */
function getParser($elem: Dom7Array): ParseElemHtmlFnType {
  for (const selector in PARSE_ELEM_HTML_CONF) {
    if ($elem[0].matches(selector)) {
      return PARSE_ELEM_HTML_CONF[selector]
    }
  }
  return defaultParser
}

/**
 * å¤çæ®é DOM elem html ï¼é span font ç­ææ¬ elem
 * @param $elem $elem
 * @param editor editor
 * @returns slate element
 */
function parseCommonElemHtml($elem: Dom7Array, editor: IDomEditor): Element[] {
  const children = genChildren($elem, editor)

  // parse
  const parser = getParser($elem)
  let parsedRes = parser($elem[0], children, editor)

  if (!Array.isArray(parsedRes)) { parsedRes = [parsedRes] } // ä¸´æ¶å¤çä¸ºæ°ç»

  parsedRes.forEach(elem => {
    const isVoid = Editor.isVoid(editor, elem)

    if (!isVoid) {
      // é void ï¼å¦ææ²¡æ children ï¼ååçº¯ææ¬
      if (children.length === 0) {
        elem.children = [{ text: $elem.text().replace(/\s+/gm, ' ') }]
      }

      // å¤ç style
      PARSE_STYLE_HTML_FN_LIST.forEach(fn => {
        elem = fn($elem[0], elem, editor) as Element
      })
    }
  })

  return parsedRes
}

export default parseCommonElemHtml
